import React from 'react';
import { Space } from 'antd';
import ErrorBoundary from 'antd/es/alert/ErrorBoundary';

import FilmItem from '../FilmItem';
import './FilmsList.css';

export default class FilmsList extends React.Component {
  render() {
    let films = this.props.films;
    try {
      let biba = [];
      let items = [];
      for (let i; i < 10; i++) {
        biba.push(i);
      }
      console.log(films);

      for (let e in this.props.films) {
        console.log(e);
        console.log(this.props.films[e]);
        console.log(films);
        let { id, image, title, date, description, genres } = this.props.films[e];
        if (e.id != undefined) {
          items.push(
            <FilmItem key={id} image={image} title={title} date={date} description={description} genres={genres} />
          );
        }
      }

      //let items = films.map((e) => {});
      console.log(items);
      return (
        <div className="items container">
          <ErrorBoundary>
            <Space size={'small'} className="items" wrap>
              Я ТУТ АЛООО
              {String(biba)}
              {String(this.props.films)}
              {items}
            </Space>
          </ErrorBoundary>
        </div>
      );
    } catch (err) {
      console.log(err);
    }
  }
}
